@import "colors";
@import "fonctions";

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: $back-color;
}

.container{

    height: 100vh;
    width: 100%;


    &-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 10%;
        background-color: lighten($back-color, 25);

        &-logo{
            @extend %logo_style;

        }

        &-nav_bar{
            list-style: none;
            background-color: transparent;

            ul{
                background-color: transparent;
            }

            li{
                display: inline-block;
                padding: 0px 20px;
                background-color: transparent;

                a{
                    transition: all 0.3s ease 0s;
                    text-decoration: none;
                    color: #fff;
                    background-color: transparent;

                    &:hover{
                        @extend %logo_style;
                    }
                }
            }
        }


            &-button{
                padding: 9px 25px;
                background-color: $princepal-color;
                border: none;
                border-radius: 50px;
                cursor: pointer;
                transition: all 0.3s ease 0s;
                text-decoration: none;
                color: #fff;

                &:hover{
                    background-color: rgba(0,136,169,0.8);
                }
            }

    }

    &-content{

        color: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 10%;
        height: 78.3vh;

        @import "first_articul";
        @import "second_articul";
        @import "third_articul";



    }

    @media only screen and (max-width:900px){
        &-content{
            display: block;
            height: 150vh;

            &-first_articul{
                margin-bottom: 20px;
                width: 520px;
                height: 300px;

                &:hover{
                    width: 520px;
                }
            }

            &-second_articul{
                margin-bottom: 20px;
                width: 520px;
                height: 300px;

                &:hover{
                    width: 520px;
                }
            }

            &-third_articul{
                margin-bottom: 20px;
                width: 520px;
                height: 300px;

                &:hover{
                    width: 520px;
                }
            }
        }
    }


    &-footer{
        height: 70px;
        width: 100%;
        padding-top:14px ;
        background-color: lighten($back-color, 25);

        &-links{
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: transparent;

            a{
                background: transparent;
                width: 150px;
                text-decoration: none;
                display: flex;
                margin-top: 10px;
                margin-left: 10px;

                img{
                    background-color: transparent;
                    width: 30px;
                    height: 26px;
                }

                h2{
                    background-color: transparent;
                    color: #111;

                    &:hover{
                        color: $princepal-color;
                    }
                }
                
                
            }

            
        }
    }

    @media only screen and (max-width:900px){
        &-footer{
            height: 180px;
            &-links{
                display: block;
            }
        }
    }

}