&-first_articul{

    background-image: url(/imgs/windows-11-windows-10-minimalism-hd-wallpaper-preview.jpg);;
    background-position: center;
    background-size: cover;
    width: 400px;
    height: 400px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 20px;
    overflow:hidden;
    transition:  0.4s;



    &:hover{
        background-image: linear-gradient(rgba(4,9,30,0.7),rgba(4,9,30,0.7)),url(/imgs/windows-11-windows-10-minimalism-hd-wallpaper-preview.jpg);
        color: #fff;
        width: 480px;
        height: 410px;
    }

    a{
        text-decoration: none;
        color: #fff;
        background: transparent;
        text-align: center;
        cursor: default;

        h1{
            background: transparent;
            padding: 10px;
            margin-top: 150px;
            transition: ease 0.7s;
        }


        h3{
            background: transparent;
            color: rgba(178, 229, 241, 0.8);
            opacity: 0;
            padding: 10px;
            height: 100%;
            margin-top: 200px;
            text-align: left;
            transition: ease 0.7s;

            
        }

    }

    &:hover h1{
        margin-top: 0;
    }

    &:hover h3{
        opacity: 1;
        margin-top: 100px;
    }


}